.signup-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}

.signup-container {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--background-color);
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
}

body {
  font-family: "Mukta", serif;
  background-color: var(--white);
}

:root {
  --white: white;
  --background-color: #F9FAFB;
  --gray: #DBDFE2;
  --button-blue: #2563EB;
}